import Papa from "papaparse";
import food from"./foodlist2.csv"
import React from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Modal from "react-bootstrap/Modal";
import { useState } from "react";
import Markdown from "react-markdown";

async function fetchModalData(file) {
  const response = await fetch(file);
  const text = await response.text();
  return text;
}
function filterPart_Of_Meal(part, food) {
  if (part === "Appetizer") {
    return food.Part_Of_Meal === "Appetizer";
  } else if (part === "Main") {
    return food.Part_Of_Meal === "Main";
  } else if (part === "Sauce") {
    return food.Part_Of_Meal === "Sauce";
  } else if (part === "Marinade") {
    return food.Part_Of_Meal === "Marinade";
  } else if (part === "Salad") {
    return food.Part_Of_Meal === "Salad";
  } else if (part === "Soup") {
    return food.Part_Of_Meal === "Soup";
  } else {
    return true;
  }
}
function filterFoodCuisine(cuisine, food) {
  if (cuisine === "Vegetarian") {
    return food.Cuisine === "Vegetarian";
  } else if (cuisine === "Seafood") {
    return food.Cuisine === "Seafood";
  } else if (cuisine === "Hungarian") {
    return food.Cuisine === "Hungarian";
  } else if (cuisine === "French") {
    return food.Cuisine === "French";
  } else if (cuisine === "Thai") {
    return food.Cuisine === "Thai";
  } else if (cuisine === "Mexican") {
    return food.Cuisine === "Mexican";
  } else if (cuisine === "PuertoRican") {
    return food.Cuisine === "PuertoRican";
  } else if (cuisine === "American") {
    return food.Cuisine === "American";
  } else if (cuisine === "Indonesian") {
    return food.Cuisine === "Indonesian";
  } else if (cuisine === "Greek") {
    return food.Cuisine === "Greek";
  } else if (cuisine === "Italian") {
    return food.Cuisine === "Italian";
  } else if (cuisine === "MiddleEastern") {
    return food.Cuisine === "MiddleEastern";
  } else if (cuisine === "Indian") {
    return food.Cuisine === "Indian";
  } else if (cuisine === "Chinese") {
    return food.Cuisine === "Chinese";
  } else if (cuisine === "Japanese") {
    return food.Cuisine === "Japanese";
  } else {
    return true;
  }
}
function filterSearch(query, food) {
  if (food.Dish.toLowerCase().includes(query.toLowerCase())) {
    return food
  }
}
function Food(props){
  const food = props.food
  const [show, setShow] = useState(false);
  const [data, setData] = useState(null);
  const handleClose = () => {
    setShow(false);
  };
  const handleShow = async () => {
    setShow(true);
    const fetchData = await fetchModalData(`recipes/${food.Recipe}`);
    setData(fetchData);
    console.log(fetchData)
  };
  //console.log(data)
  return(
    <div style={{margin: "10px",}} >
         <Card style={{ width: "18rem" }}>
        <Card.Img
          variant="top"
          src={`images/${food.Image}`}
          alt={food.Dish}
        />
        <Card.Body>
          <Card.Title>{food.Dish}</Card.Title>
          <Button variant="primary" onClick={handleShow}>
          Recipe
          </Button>
        </Card.Body>
      </Card>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>{food.Dish}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Markdown>{data}</Markdown>
        </Modal.Body>
      </Modal>

    </div>
  )
}

class App extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      search: "",
      part: "",
      cuisine: "",
    };
  }
  async componentDidMount() {
    const response = await fetch(food);
    const csvText = await response.text();
    const parsedData = Papa.parse(csvText, { header: true }).data;
    this.setState({ data: parsedData });
  }
  render(){
    const { data ,part,cuisine,search} = this.state;
    if (data === null) {
      return "loading...";
      }
    const allfoods = data.length;
  
    const filteredFoods = data.filter(
      (food) => filterPart_Of_Meal(part, food)&&filterFoodCuisine(cuisine,food)&& filterSearch(search, food)
    );   
    const foods = filteredFoods.map((food)=>{
      return<Food food={food} key={food.Dish} />
    })
    const newcfoods = Food.length;

    return(
      <div>
        {/* <h1>rrrrrrrrrrrrrr</h1>
        <ul>
          {data.map(cocktail => <li>{cocktail['Dish']}</li>)}
        </ul>  */}
         <div style={{display:"flex",justifyContent:"center",marginTop:"2rem"}}>
          <label htmlFor="part">Choose a meal</label>
          <select
            id="part"
            onChange={(event) => {
              this.setState({ part: event.target.value });
            }}
          >
            <option value="all">Show all</option>
            <option value="Appetizer">Appetizer</option>
            <option value="Main">Main</option>
            <option value="Sauce">Sauce</option>
            <option value="Marinade">Marinade</option>
            <option value="Salad">Salad</option>
            <option value="Soup">Soup</option>
          </select>

          <label htmlFor="cuisine">Choose a cuisine</label>
          <select
            id="cuisine"
            onChange={(event) => {
              this.setState({ cuisine: event.target.value });
            }}
          >
            <option value="all">Show all</option>
            <option value="Vegetarian">Vegetarian</option>
            <option value="Seafood">Seafood</option>
            <option value="Hungarian">Hungarian</option>
            <option value="French">French</option>
            <option value="Mexican">Mexican</option>
            <option value="PuertoRican">PuertoRican</option>
            <option value="American">American</option>
            <option value="Indonesian">Indonesian</option>
            <option value="Greek">Greek</option>
            <option value="Italian">Italian</option>
            <option value="MiddleEastern">MiddleEastern</option>
            <option value="Indian">Indian</option>
            <option value="Thai">Thai</option>
            <option value="Chinese">Chinese</option>
            <option value="Japanese">Japanese</option>
          </select>
          <label htmlFor="search">search</label>
          <input
            id="search"
            onChange={(event) => {
              this.setState({ search: event.target.value });
            }}
          ></input>
        </div>
        <h1 style={{textAlign: "center"}}>
          Now showing {newcfoods} out of {allfoods} cocktails
        </h1>
        <div 
          className="App"
          style={{
            display: "flex",
            width: "80%",
            margin: "10px auto",
            flexWrap: "wrap",
            justifyContent: "center",
            
          }}
        >
          {foods}
        </div>
      </div>
    )
  }
}
export default App;